import useStore  from '../../../stores/useStore';
import shallow from 'zustand/shallow';
import EditComponent from './EditComponent';
import { v4 as uuidv4 } from 'uuid';
import moment from 'moment';

const getState = state => [
    state.states,
    state.subject,
    state.selectedState,
    state.changeSubject,
    state.changeState,
    state.whoApplied,
    state.changeWhoApplied,
    state.applyDate,
    state.changeApplyDate,
    state.toSecretary,
    state.changeToSecretary,
    state.additionalCommn,
    state.changeSetAdditionalCommn,
    state.decisionStatus,
    state.changeDecisionStatus,
    state.approveDate,
    state.changeApproveDate,
    state.protocolDate,
    state.changeProtocolDate,
    state.protocol,
    state.changeProtocol,
    state.note1,
    state.changeNote1,
    state.tasks,
    state.changeTasks,
    state.steps,
    state.changeSteps,
    state.executionStatus,
    state.changeExecutionStatus,
    state.note2,
    state.changeNote2,
    state.note3,
    state.changeNote3,
    state.changeFiles,
    state.insertAppeal
]

const Edit = () => {
    const [
        states, 
        subject, 
        selectedState,
        changeSubject,
        changeState,
        whoApplied,
        changeWhoApplied,
        applyDate,
        changeApplyDate,
        toSecretary,
        changeToSecretary,
        additionalCommn,
        changeSetAdditionalCommn,
        decisionStatus,
        changeDecisionStatus,
        approveDate,
        changeApproveDate,
        protocolDate,
        changeProtocolDate,
        protocol,
        changeProtocol,
        note1,
        changeNote1,
        tasks,
        changeTasks,
        steps,
        changeSteps,
        executionStatus,
        changeExecutionStatus,
        note2,
        changeNote2,
        note3,
        changeNote3,
        changeFiles,
        insertAppeal
    ] = useStore(getState, shallow);
    const addNewAppeal = () => {
        let appeal = {
            aid: uuidv4(),
            nzid: selectedState,
            whoapplied: whoApplied,
            applyDate: applyDate,
            tosecretary: toSecretary,
            attch: null,
            additionalcommn: additionalCommn,
            status: decisionStatus,
            protocol: protocol,
            approvedate: approveDate,
            protocoldate: protocolDate,
            note1: note1,
            tasksbyboard: tasks,
            stepsbykim: steps,
            taskstatus: executionStatus,
            note2: note2,
            notebyaih: note3,
            enterdate: null,
            performerid: window.currentUserId
        }
        insertAppeal(appeal);
    }
    const args = {
        states,
        subject,
        selectedState,
        changeSubject,
        changeState,
        whoApplied,
        changeWhoApplied,
        applyDate: moment(applyDate).format('YYYY-MM-DD'),
        changeApplyDate,
        toSecretary: moment(toSecretary).format('YYYY-MM-DD'),
        changeToSecretary,
        additionalCommn,
        changeSetAdditionalCommn,
        decisionStatus,
        changeDecisionStatus,
        approveDate: moment(approveDate).format('YYYY-MM-DD'),
        changeApproveDate,
        protocolDate: moment(protocolDate).format('YYYY-MM-DD'),
        changeProtocolDate,
        protocol,
        changeProtocol,
        note1,
        changeNote1,
        tasks,
        changeTasks,
        steps,
        changeSteps,
        executionStatus,
        changeExecutionStatus,
        note2,
        changeNote2,
        note3,
        changeNote3,
        changeFiles,
        addNewAppeal
    }
    return (
       <EditComponent {...args} />
    );
};

export default Edit;
