import { useNavigate } from 'react-router-dom';

const ButtonElement = props => {
    const navigate = useNavigate();

    const goToEditPanel = e => {
        navigate(
            window.baseUrl +
                '/app/edit' +
                '?func=ll&objId=' +
                window.currentWebreportId +
                '&objAction=RunReport',
            { state: { editType: props.editType } }
        );
    };
    return (
        <button className={props.className} onClick={e => goToEditPanel()}>
            {props.children}
        </button>
    );
};

export default ButtonElement;
