import React from 'react';

import ButtonElement from './ButtonElement';
import MainElement from './DataCell';

const MainContent = props => {
    return (
        <React.Fragment>
            <MainElement
                id="0"
                element={props.data.subject}
                style={props.style}
            >
                <ButtonElement className="item-edit" editType="save">
                    <i className="fas fa-pen"></i>
                </ButtonElement>
            </MainElement>
            <MainElement
                id="1"
                element={props.data.applier}
                style={props.style}
            />
            <MainElement
                id="2"
                element={props.data.apply_date}
                style={props.style}
            />
            <MainElement
                id="3"
                element={props.data.protocol_date}
                style={props.style}
            />
            <MainElement
                id="4"
                element={props.data.presented_file}
                style={props.style}
            />
            <MainElement
                id="5"
                element={props.data.additional_action}
                style={props.style}
            />
            <MainElement
                id="6"
                element={props.data.status}
                style={props.style}
            />
            <MainElement
                id="7"
                element={props.data.protocol_number}
                style={props.style}
            />
            <MainElement
                id="8"
                element={props.data.submit_date}
                style={props.style}
            />
            <MainElement
                id="9"
                element={props.data.protocol_date}
                style={props.style}
            />
            <MainElement id="10" element="Qeyd" style={props.style} />
            <MainElement id="11" element="" style={props.style} />
            <MainElement id="12" element="" style={props.style} />
            <MainElement id="13" element="" style={props.style} />
            <MainElement id="14" element="" style={props.style} />
            <MainElement id="15" element="" style={props.style} />
        </React.Fragment>
    );
};

export default MainContent;
