const EditComponent = props => {
    return  <div id="body">
        <main className="content">
            <form>
                <div className="form-group">
                    <div className="form-group col-md-6">
                        <label>State</label>
                        <select 
                            className="form-control" 
                            value={props.selectedState}
                            onChange={(e) => props.changeState(e.target.value)}>
                            <option value="none" disabled>Choose...</option>
                            {
                                props.states.length > 0 && props.states.map(x => {
                                    return <option 
                                            key={x.id} 
                                            value={x.id}> {x.name} </option>
                                })
                            }
                        </select>
                    </div>
                </div>
                <div className="form-group">
                    <label>Mvzu</label>
                    <input
                        type="text"
                        className="form-control"
                        value={props.subject}
                        onChange={e => props.changeSubject(e.target.value)}
                    />
                </div>
                <div className="form-row row">
                    <div className="form-group col-4">
                        <label>Mracit edn </label>
                        <input
                            type="text"
                            className="form-control"
                            placeholder="Mracit edn"
                            value={props.whoApplied}
                            onChange={e => props.changeWhoApplied(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-4">
                        <label>Mracit tarixi</label>
                        <input
                            type="date"
                            className="form-control"
                            value={props.applyDate}
                            onChange={e => props.changeApplyDate(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-4">
                        <label>
                            Tqdim edilmi sndlr
                        </label>
                        <input
                            id="fileX"
                            type="file"
                            className="form-control"
                            multiple
                            onChange={e=>props.changeFiles(e.target.files)}
                        />
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-md-4">
                        <label>
                            Mracitin Korporativ Katib tqdim edilmsi
                        </label>
                        <input
                            type="date"
                            className="form-control"
                            value = {props.toSecretary}
                            onChange = {e => props.changeToSecretary(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-md-8">
                        <label>
                            lav kommunikasiya tdbirlri
                        </label>
                        <textarea
                            className="form-control"
                            value={props.additionalCommn}
                            onChange={e=>props.changeSetAdditionalCommn(e.target.value)}
                        ></textarea>
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-3">
                        <label>
                            M-nn qrarnn statusu
                        </label>
                        <input
                            type="text"
                            className="form-control"
                            value={props.decisionStatus}
                            onChange={e => props.changeDecisionStatus(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-3">
                        <label>Tsdiq tarixi</label>
                        <input
                            type="date"
                            className="form-control"
                            value={props.approveDate}
                            onChange={e=>props.changeApproveDate(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-3">
                        <label>
                            Protokol daxil olub
                        </label>
                        <input
                            type="date"
                            className="form-control"
                            value={props.protocolDate}
                            onChange={e=>props.changeProtocolDate(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-2">
                        <label>Protokol </label>
                        <input
                            type="number"
                            className="form-control"
                            value={props.protocol}
                            onChange={e=>props.changeProtocol(e.target.value)}
                        />
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-4">
                        <label>Qeyd</label>
                        <textarea
                            className="form-control"
                            value={props.note1}
                            onChange={e=>props.changeNote1(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label>
                            M-nn qrarndan irli gln taprqlar
                        </label>
                        <textarea
                            className="form-control"
                            value={props.tasks}
                            onChange={e => props.changeTasks(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label>
                            Taprqlarn icras istiqamtind KM
                            trfindn atlan addmlar
                        </label>
                        <textarea
                            className="form-control"
                            value={props.steps}
                            onChange={e=>props.changeSteps(e.target.value)}
                        ></textarea>
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-4">
                        <label>
                            M-nn qrarndan irli gln taprqlarn icra
                            statusu
                        </label>
                        <textarea
                            className="form-control"
                            value={props.executionStatus}
                            onChange={e=>props.changeExecutionStatus(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label>Qeyd 2</label>
                        <textarea
                            className="form-control"
                            value={props.note2}
                            onChange={e=>props.changeNote2(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label>Qeyd 3</label>
                        <textarea
                            className="form-control"
                            value={props.note3}
                            onChange={e=>props.changeNote3(e.target.value)}
                        ></textarea>
                    </div>
                </div>
                <div className="form-group row">
                    <div className="col-sm-12 button-right">
                        <button type="button" className="btn-save" onClick={props.addNewAppeal}>
                            Save
                        </button>
                        <button type="button" className="btn-reset">
                            Reset
                        </button>
                    </div>
                </div>
            </form>
        </main>
    </div>
}

export default EditComponent;