const handleGetStates = async (set, get) => {
    try{
        const res = await axios.get("https://ecm.socar.az/otcs/llisapi.dll?func=ll&objId=186063405&objAction=RunReport");
        const data = res.data.result;
        data.pop();
        set({
            states: data
        })
    }catch(e){
        //console.log(e);
    }   
}

const handleCreateFiles = async (set, get) => {
    const createFile = async (file) => {
		const myHeaders = new Headers();
		myHeaders.append("OTCSTicket", window.OTCSTicket);

		const formdata = new FormData();
		formdata.append("type", "144");
		formdata.append("parent_id", "186174338");
		formdata.append("name", file.name);
		formdata.append("file", file);

		const requestOptions = {
  			method: 'POST',
  			headers: myHeaders,
  			body: formdata,
  			redirect: 'follow'
		};
        try{
            const response = await fetch("https://ecm.socar.az/otcs/llisapi.dll/api/v2/nodes", requestOptions);
  		    const json = await response.json();
            return json.results.data.properties.id;
        }catch(e){
            alert("File with this name alread existed!");
            throw e;
        }
	}
    const createdFiles = get().files;
    let files = [];
	for await (const el of createdFiles) {
  		const id = await createFile(el);
        files.push(id);
	}
    set({
        files
    })
}

const store = (set, get) => ({
    states: [],
    subject: '',
    selectedState: "none",
    whoApplied: '',
    applyDate: '',
    toSecretary: '',
    additionalCommn: '',
    decisionStatus: '',
    approveDate: '',
    protocolDate: '',
    protocol: 0,
    note1: '',
    tasks: '',
    steps: '',
    executionStatus: '',
    note2: '',
    note3: '',
    files: [],
    insertCharter: async (id, charter) => await handleInsertCharter(set, get, id, charter),
    insertAppeal: async appeal => await handleInsertAppeal(set, get, appeal),
    createFiles: async () => await handleCreateFiles(set, get),
    changeSubject: value => handleChangeSubject(set, get, value),
    changeState: value => handleChangeState(set, get, value),
    changeWhoApplied: value => handleWhoApplied(set, get, value),
    changeApplyDate: value => handleApplyDate(set, get, value),
    changeToSecretary: value => handleChangeSecretary(set, get, value),
    changeSetAdditionalCommn: value => handleChangeSetAdditionalCommn(set, get, value),
    changeDecisionStatus: value => handleChangeDecisionStatus(set, get, value),
    changeApproveDate: value => handleChangeApproveDate(set, get, value),
    changeProtocolDate: value => handleChangeProtocolDate(set, get, value),
    changeProtocol: value => handleChangeProtocol(set, get, value),
    changeNote1: value => handleChangeNote1(set, get, value),
    changeTasks: value => handleTasks(set, get, value),
    changeSteps: value => handleSteps(set, get, value),
    changeExecutionStatus: value => handleChangeExecutionStatus(set, get, value),
    changeNote2: value => handleChangeNote2(set, get, value),
    changeNote3: value => handleChangeNote3(set, get, value),
    changeFiles: value => handleFileChange(set, get, value),
    getStates: async () => await handleGetStates(set, get)
});

const useStore = create(devtools(store));

export default useStore;

