import data from '../../../MOCK_DATA.json';
import React, { useState } from 'react';
import TableComponent from './TableComponent';
import { useNavigate } from 'react-router-dom';
import useStore  from '../../../stores/useStore';
import shallow from 'zustand/shallow';

const getState = state => [
    state.states,
    state.changeState
]

const Table = () => {
    const [filteredData, setFilteredData] = useState(data);
    const [ states, changeState ] = useStore(getState, shallow);
    const navigate = useNavigate();

    const tableColumns = [
        { id: 0, name: 'Mvzu' },
        { id: 1, name: 'Mracit edn' },
        { id: 2, name: 'Mracit tarixi' },
        { id: 3, name: 'Mracitin Korporativ Katib tqdim edilmsi' },
        { id: 4, name: 'Tqdim edilmi sndlr' },
        { id: 5, name: 'lav kommunikasiya tdbirlri' },
        { id: 6, name: 'M-nn qrarnn statusu' },
        { id: 7, name: 'Protokol ' },
        { id: 8, name: 'Tsdiq tarixi' },
        { id: 9, name: 'Protokol daxil olub' },
        { id: 10, name: 'Qeyd' },
        { id: 11, name: 'M-nn qrarndan irli gln taprqlar' },
        {
            id: 12,
            name: 'Taprqlarn icras istiqamtind KM trfindn atlan addmlar',
        },
        {
            id: 13,
            name: 'M-nn qrarndan irli gln taprqlarn icra statusu',
        },
        { id: 14, name: 'Qeyd 2' },
        { id: 15, name: 'Qeyd 3' },
    ];

    const checkData = e => {
        let updatedData = [];

        data.map(data_node => {
            if (parseInt(e.target.id) === 1) {
                updatedData = data.filter(el =>
                    el.applier.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 2) {
                updatedData = data.filter(el =>
                    el.apply_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 3) {
                updatedData = data.filter(el =>
                    el.presented_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 4) {
                updatedData = data.filter(el =>
                    el.presented_file.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 5) {
                updatedData = data.filter(el =>
                    el.additional_action.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 6) {
                updatedData = data.filter(el =>
                    el.status.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 7) {
                updatedData = data.filter(el =>
                    el.protocol_number.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 8) {
                updatedData = data.filter(el =>
                    el.submit_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 9) {
                updatedData = data.filter(el =>
                    el.protocol_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else {
                updatedData = data.filter(el =>
                    el.subject.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            }
        });
    };

    const addNew = (id) => {
        changeState(id);
        navigate(window.baseUrl + '/app/add' + '?func=ll&objId=' + window.currentWebreportId + '&objAction=RunReport');
    }
    
    const clickAndDrag = e => {
        if (e.target.classList.contains('accordion')) {
            console.log(e.target.classList);
            const slider = document.querySelector('.main-container');
            let isDown = false;
            let startX;
            let scrollLeft;

            slider.addEventListener('mousedown', e => {
                isDown = true;
                slider.classList.add('active');
                startX = e.pageX - slider.offsetLeft;
                scrollLeft = slider.scrollLeft;
            });
            slider.addEventListener('mouseleave', () => {
                isDown = false;
                slider.classList.remove('active');
            });
            slider.addEventListener('mouseup', () => {
                isDown = false;
                slider.classList.remove('active');
            });
            slider.addEventListener('mousemove', e => {
                if (!isDown) return;
                e.preventDefault();
                const x = e.pageX - slider.offsetLeft;
                const walk = (x - startX) * 3; //scroll-fast
                slider.scrollLeft = scrollLeft - walk;
            });
        } else {
            console.log('hey');
        }
    }

    const args = {
        states,
        filteredData,
        setFilteredData,
        tableColumns,
        checkData,
        addNew,
        clickAndDrag
    }

    return (
        <TableComponent { ...args } />
    );
};

export default Table;
